@import "../../global.scss";

.topbar {
    width: 100%;
    height: 70px;
    background-color: $primary;
    color: $secondary;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all .5s ease;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;

            .logo {
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }

            .item-container {
                display: flex;
                align-items: center;
                margin: 0 30px;

                .icon {
                    font-size: 18px;
                    margin-right: 5px;
                }

                span {
                    font-size: 17px;
                    font-weight: 400;
                }
            }
        }

        .right {

            .hamburger{
                margin-left: 90%;
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                overflow: hidden;
            }

            span{
                width: 100%;
                height: 3px;
                background-color: $secondary;
                transform-origin: left;
                transition: all 1s ease;
            }
        }
    }

    &.active{
        background-color: $primaryDark;
        color: $white;

        .hamburger{

            span{
                &:first-child{
                    background-color: $white;
                    transform: rotate(45deg);
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:last-child{
                    background-color: $white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}